/* #base
   -------------------------------------------------------------------------- */
/**
 * JavaScriptでブレイクポイントを取得するために指定しています。
 * /assets/js/lib/breakpoint.js を参照してください。
 */
body:before {
  content: "";
  display: none;
}

@media (min-width: 768px) {
  body:before {
    content: "md";
  }
}

@media (min-width: 1080px) {
  body:before {
    content: "lg";
  }
}

@media (min-width: 1440px) {
  body:before {
    content: "xl";
  }
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 87.5%;
  line-height: 1.5;
}

@media (min-width: 768px) {
  html {
    font-size: 100%;
  }
}

body {
  color: #000;
  font-family: "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  font-kerning: normal;
  background-color: #fff;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/**
 * `dt`と`dd`の左端を揃えます。
 */
dd {
  margin-left: 0;
}

/**
 * 日本語では斜体を使用しないためリセットします。
 */
[lang="ja"] i,
[lang="ja"] cite,
[lang="ja"] em,
[lang="ja"] address,
[lang="ja"] dfn {
  font-style: normal;
}

/**
 * フルードイメージにします。
 */
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/* imgとonject要素でSVGをフルードイメージにします。 */
[src$=".svg"],
[data$=".svg"] {
  width: 100%;
  /* IE対応 */
}

code {
  font-family: Consolas, Menlo, Courier, monospace;
  font-size: 0.85em;
}

/* IE10以降`input[type="text"]`の入力内容の消去を非表示にします。 */
input::-ms-clear {
  visibility: hidden;
}

/* IE10以降の`input[type="password"]`のマスクの一時的解除を非表示にします。 */
input::-ms-reveal {
  visibility: hidden;
}

/* Normalize.cssをリセットします。 */
figure {
  margin: 0;
}

/* ブロック要素の上下のマージンをリセットします。 */
h1, h2, h3, h4, h5, h6,
ul, ol, dl,
blockquote, p, address,
hr,
table,
fieldset, figure,
pre {
  margin-top: 0;
  margin-bottom: 0;
}

/**
 * sw-(SiteWide)...サイト共通の小さなパーツ
 * st-(Structure)...サイト共通のレイアウト
 * tp-(TopPage)...サイトトップページ
 */
/* #Break
   -------------------------------------------------------------------------- */
/*doc
---
name: Break
category: SiteWide
tag: Text
---

