/* #Button
   -------------------------------------------------------------------------- */
/*doc
---
name: Button
category: SiteWide
tag: [Button, Link, Form]
---

ボタンのデフォルトスタイルです。

```jade
a.sw-Button(href="#") aタグボタン
a.sw-Button.sw-Button-disabled(href="#") aタグボタン
button.sw-Button(type="button") butotnタグボタン
button.sw-Button(disabled type="button") butotnタグボタン
input.sw-Button(type="button" value="inputボタン")
input.sw-Button(type="button" value="inputボタン" disabled)
```

`sw-Button()`mixinで他のボタンにもスタイルを継承することができます。

```scss
.AnotherButton {
  @include sw-Button();
}
```
*/
.sw-Button {
  display: inline-block;
  margin: 0;
  padding: 0.75em 2em;
  border: none;
  border: 1px solid #d0d0d0;
  color: inherit;
  font-family: inherit;
  font-size: 1em;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  background: transparent;
  background-color: #fff;
  appearance: none;
  cursor: pointer;
}

.sw-Button:hover, .sw-Button:active, .sw-Button:focus {
  text-decoration: none;
}

.sw-Button:focus {
  outline: 0;
  box-shadow: 0 0 5px #1589ee;
}

.sw-Button:disabled, .sw-Button-disabled {
  opacity: 0.5;
  border-color: #ddd;
  background-color: #ddd;
  cursor: not-allowed;
}

.sw-Button-disabled {
  pointer-events: none;
}

/* #Delimiter
   -------------------------------------------------------------------------- */
/*doc
---
name: Delimiter
category: SiteWide
tag: Text
---

途中で改行をさせたくないフレーズやキーワードに指定します。囲まれた範囲（Delimiter）がまとまって改行されます。

```jade
p テキストは基本的にスペースや役物を基準に改行されますが、
  span.sw-Delimiter この部分はまとめて改行されます。
  | ここは通常のテキストです。
```
*/
.sw-Delimiter {
  display: inline-block;
}

/* #Divider
   -------------------------------------------------------------------------- */
/*doc
---
name: Divider
category: SiteWide
tag: Text
---

`<hr>`のような区切り記号を使って、分割・グルーピングします。

```jade
p テキストです。
hr.sw-Divider
p テキストです。
```
*/
.sw-Divider {
  box-sizing: inherit;
  display: block;
  width: 100%;
  height: auto;
  margin: 1.2em auto;
  border: 0;
  border-top: 1px solid #e5e5e5;
}

/* #Embed
   -------------------------------------------------------------------------- */
/*doc
---
name: Embed
category: SiteWide
tag: ThirdPartyComponent
---

Youtubeなどをレスポンシブ対応させます。

```jade
.sw-Embed.
  <iframe width="560" height="315" src="https://www.youtube.com/embed/1OKZOV-iLj4"></iframe>
```
*/
.sw-Embed {
  display: block;
  overflow: hidden;
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
}

.sw-Embed_Item,
.sw-Embed iframe,
.sw-Embed embed,
.sw-Embed object,
.sw-Embed video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

/* #FormCheckbox
   -------------------------------------------------------------------------- */
/*doc
---
name: FormCheckbox
category: SiteWide
tag: Form
---

`type="checkbox"`のデフォルトスタイルです。

```jade
input.sw-FormCheckbox(type="checkbox" id="checkbox1" checked)
label(for="checkbox1") チェックボックス

input.sw-FormCheckbox(type="checkbox" id="checkbox2")
label(for="checkbox2") チェックボックス

input.sw-FormCheckbox(disabled type="checkbox" id="checkbox3")
label(disabled for="checkbox3") チェックボックス
```

`sw-FormCheckbox()`mixinで他のボタンにもスタイルを継承することができます。

```scss
.AnotherFormCheckbox {
  @include sw-FormCheckbox();
}
```
*/
.sw-FormCheckbox {
  /* デフォルトのチェックボックスを非表示にする。 */
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.sw-FormCheckbox + label {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.sw-FormCheckbox + label:before {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.25em;
  vertical-align: text-bottom;
  background-image: url("/assets/img/SiteWide/Icon/FormCheckbox_off.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.sw-FormCheckbox:focus + label:before {
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

.sw-FormCheckbox:checked + label:before {
  background-image: url("/assets/img/SiteWide/Icon/FormCheckbox_on.svg");
}

.sw-FormCheckbox:disabled,
.sw-FormCheckbox + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

/* #FormInput
   -------------------------------------------------------------------------- */
/*doc
---
name: FormInput
category: SiteWide
tag: Form
---

`<input>`タグのデフォルトスタイルです。

```jade
input.sw-FormInput(type="input" placeholder="デフォルト")

label.sw-FormInput_TextTop(for="input1") inputタグの説明です
input.sw-FormInput(type="input" name="email" autocomplete="email" id="input1" placeholder="example@gmail.com")

input.sw-FormInput(type="input" name="name" autocomplate="name" id="input2" placeholder="山田 太郎")
label.sw-FormInput_TextTop(for="input2") inputタグの説明です

input.sw-FormInput(disabled type="input" placeholder="disabled")
```

`sw-FormInput()`mixinで他のボタンにもスタイルを継承することができます。

```scss
.AnotherFormInput {
  @include sw-FormInput();
}
```
*/
.sw-FormInput {
  box-sizing: border-box;
  width: 100%;
  height: 42px;
  padding: 0 1em;
  border: 1px solid #d0d0d0;
  border-radius: 3px;
  font-size: 16px;
  vertical-align: middle;
  background-color: #fff;
  appearance: none;
  box-shadow: 1px 1px 6px -3px #9e9e9e inset;
  /**
   * Normalize.cssのリセットをします。
   */
}

.sw-FormInput[type="search"] {
  box-sizing: border-box;
  appearance: none;
}

.sw-FormInput::placeholder {
  color: #767676;
  font-size: 0.85em;
}

.sw-FormInput:focus {
  outline: 0;
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

.sw-FormInput:disabled {
  opacity: 0.5;
  background-color: #ddd;
  cursor: not-allowed;
}

.sw-FormInput_TextTop {
  display: inline-block;
  margin-bottom: 0.25em;
}

.sw-FormInput_TextBottom {
  display: inline-block;
  margin-top: 0.25em;
}

