/* #FormSelect
   -------------------------------------------------------------------------- */
/*doc
---
name: FormSelect
category: SiteWide
tag: Form
---

`<select>`タグのデフォルトスタイルです。

```jade
.sw-FormSelect
  select
    option(value="default") 選択してください
    option(value="hokkaido") 北海道
    option(value="akita") 秋田

.sw-FormSelect.is-disabled
  select(disabled)
    option(value="default") 選択してください
    option(value="hokkaido") 北海道
    option(value="akita") 秋田
```

`sw-FormSelect()`mixinで他のボタンにもスタイルを継承することができます。

```scss
.AnotherFormSelect {
  @include sw-FormSelect();
}
```
*/
.sw-FormSelect {
  position: relative;
  /* Firefox Hack */
  /* IE10以上 */
  /* IE9ではデフォルトの矢印アイコンを表示する。 */
}

.sw-FormSelect:before {
  content: "";
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
  left: 0;
  width: 99%;
  width: calc(100% - 6px);
  margin: auto;
  box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.05);
}

.sw-FormSelect:after {
  content: "";
  position: absolute;
  top: 0;
  right: 1em;
  bottom: 0;
  width: 1em;
  height: 1em;
  margin: auto;
  background-image: url("/assets/img/SiteWide/Icon/FormSelect.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  pointer-events: none;
}

.sw-FormSelect.is-disabled:after {
  opacity: 0.5;
}

.sw-FormSelect > select {
  width: 100%;
  height: 2.5em;
  padding: 0 3em 0 1em;
  border: 1px solid #d0d0d0;
  border-radius: 3px;
  font-size: 16px;
  line-height: 1;
  vertical-align: middle;
  background-color: #fff;
  appearance: none;
  box-shadow: 1px 1px 6px -3px #9e9e9e inset;
}

.sw-FormSelect > select:focus {
  outline: 0;
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

.sw-FormSelect > select:disabled {
  opacity: 0.5;
  background-color: #ddd;
  cursor: not-allowed;
}

.sw-FormSelect > select {
  text-indent: 0.01px;
  text-overflow: "";
}

.sw-FormSelect > select::-ms-expand {
  display: none;
}

.sw-FormSelect:not(:target):after {
  background-image: none\9;
}

.sw-FormSelect > select:not(:target) {
  padding-right: 0\9;
}

